.expert {
  margin-bottom: 90px;
  background-image: url(../img/expert-bg.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center bottom;

  @media (max-width: (1199px)) {
    padding-top: 100px;
  }

  &__inner {
    display: flex;
    align-items: flex-end;
    justify-content: space-between;

    @media (max-width: (757px)) {
      justify-content: center;
    }
  }

  &__content {
    color: $color-white;
    max-width: 360px;
    width: 100%;
    margin-bottom: 55px;

    @media (max-width: (1350px)) {
      margin-bottom: 20px;
    }
  }

  &__title {
    @media (max-width: (1199px)) {}
  }

  &__text {
    @include line-clamp(4);
    margin-bottom: 14px;
    max-width: 330px;
    font-weight: 400;
    letter-spacing: 1.15px;

    @media (max-width: (757px)) {
      font-size: 14px;
    }
  }

  &__link {
    display: inline-block;
  }

  &__img {
    max-width: 478px;
    width: 100%;
    max-height: 547px;
    height: 100%;
    object-fit: cover;
    object-position: center;
    margin-right: 100px;

    @media (max-width: (1350px)) {
      max-width: 378px;
      max-height: 447px;
    }

    @media (max-width: (1199px)) {
      margin-right: 30px;
    }

    @media (max-width: (757px)) {
      display: none;
    }
  }
}
