.nice {
  margin-bottom: 60px;

  @media (max-width: (991px)) {
    margin-bottom: 50px;
  }

  &__wraper {
    display: flex;
    justify-content: space-between;
    margin-bottom: 60px;

    @media (max-width: (757px)) {
      flex-wrap: wrap;
      justify-content: center;
      text-align: center;
    }
  }

  &__title {
    max-width: 455px;
    width: 100%;
    margin-right: 15px;
  }

  &__subtitle {
    @include line-clamp(3);
    max-width: 400px;
    width: 100%;
    font-weight: 400;
  }

  &__list {
    display: flex;
    margin: 0 20px;

    @media (max-width: (991px)) {
      flex-wrap: wrap;
      justify-content: center;
      margin: 0;
    }
  }

  &__item {
    position: relative;
    margin: 0 15px;

    &--last {
      &::after {
        content: '';
        position: absolute;
        width: 80px;
        height: 80px;
        background-image: url(../img/last-card.svg);
        background-repeat: no-repeat;
        background-size: cover;
        background-position: center;
        top: -40px;
        right: -40px;

        @media (max-width: (991px)) {
          display: none;
        }
      }
    }

    @media (max-width: (991px)) {
      margin-bottom: 10px;
    }

    @media (max-width: (575px)) {
      margin: 0 0 10px 0;
    }
  }

  &__card {
    max-width: 350px;
    width: 100%;
    position: relative;
    overflow: hidden;
    box-shadow: transparent;
    transition: box-shadow 0.3s;

    &:hover {
      box-shadow: 0px 10px 52px rgba(0, 0, 0, 0.08);

      .nice__location {
        color: $color-acсent;
      }
    }

    &--video {
      &::before {
        content: '';
        position: absolute;
        width: 50px;
        height: 50px;
        background-image: url(../img/Play-btn.svg);
        background-repeat: no-repeat;
        background-size: cover;
        background-position: center;
        top: 134px;
        right: 20px;
      }
    }
  }

  &__img {
    width: 350px;
    height: 204px;
    object-fit: cover;
    object-position: center;
    margin-bottom: 30px;
  }

  &__inner {
    padding: 0 29px 31px 29px;
  }

  &__text {
    @include line-clamp(4);
    font-weight: 400;
    letter-spacing: 1.15px;
    margin-bottom: 25px;

    @media (max-width: (575px)) {
      font-size: 14px;
    }
  }

  &__author {
    @include line-clamp(2);
    display: block;
    font-size: 18px;
  }

  &__location {
    @include line-clamp(2);
    color: $color-bg;
    font-weight: 400;
    transition: color 0.3s;
  }

  &__link {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
  }


}
