.best {
  margin-bottom: 145px;

  @media (max-width: (991px)) {
    margin-bottom: 50px;
  }

  &__container {
    margin: 0 auto;
    padding: 0 15px;
    max-width: 1470px;
    overflow: hidden;
  }

  &__inner {
    display: flex;
    justify-content: space-between;

    @media (max-width: (991px)) {
      justify-content: center;
    }
  }

  &__left {
    display: flex;
    margin-right: 20px;
    max-width: 840px;
    width: 100%;
    position: relative;

    @media (max-width: (991px)) {
      display: none;
    }
  }

  &__img {
    height: 550px;
    object-fit: cover;
    object-position: center;
    border-radius: 20px;

    @media (max-width: (1199px)) {
      height: 350px;
    }

    &--left {
      margin: 0 22px 0 -80px;

      @media (max-width: (1199px)) {
        margin: 0 22px;
      }
    }

    &--right {
      border-bottom-right-radius: 0;
    }
  }

  &__card {
    position: absolute;
    top: 300px;
    right: 90px;
    width: 300px;
    height: 140px;
    padding: 32px 29px 32px 94px;
    overflow: hidden;
    background-color: $color-white;
    border-radius: 20px;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);

    &::before {
      content: '';
      position: absolute;
      width: 45px;
      height: 45px;
      background-image: url(../img/card-icon.svg);
      background-repeat: no-repeat;
      background-size: cover;
      background-position: center;
      top: 45px;
      left: 29px;
    }

    @media (max-width: (1199px)) {
      display: none;
    }
  }

  &__card-title {
    @include line-clamp(2);
  }

  &__card-subtitle {
    @include line-clamp(2);
    font-weight: 400;
  }

  &__right {
    display: flex;
    flex-direction: column;
    justify-content: center;
    max-width: 375px;
    width: 100%;
    margin-right: 180px;

    @media (max-width: (1350px)) {
      margin-right: 0;
    }

    @media (max-width: (991px)) {
      align-items: center;
      text-align: center;
    }
  }

  &__text {
    font-weight: 400;
    margin-bottom: 40px;

    @media (max-width: (1199px)) {
      font-size: 14px;
    }
  }


  &__btn {
    max-width: 172px;
  }
}
