.serv {
  text-align: center;
  background-color: #f5f6fa;

  &__title {
    padding-top: 415px;

    @media (max-width: (991px)) {
      padding-top: 250px;
    }
  }

  &__subtitle {
    font-weight: 400;
    padding-bottom: 228px;
    @include line-clamp(2);
  }

  &__cards {
    display: flex;
    justify-content: center;
    margin: -200px 10px 112px;

    @media (max-width: (991px)) {
      margin-bottom: 50px;
    }

    @media (max-width: (575px)) {
      flex-wrap: wrap;
    }
  }

  &__card {
    max-width: 540px;
    width: 100%;
    min-height: 365px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    background-color: $color-white;
    border-radius: 25px;
    margin: 0 15px;
    overflow: hidden;
    position: relative;
    transition: color 0.5s, background-color 0.5s;

    &:hover {
      background-color: $color-acсent;
      color: $color-white;

      .serv__svg {
        fill: $color-white;
      }
    }

    @media (max-width: (575px)) {
      min-height: 300px;
      margin-bottom: 20px;
    }
  }

  &__svg {
    height: 70px;
    width: 70px;
    margin: 40px 0 30px;
    fill: $color-acсent;
    transition: fill 0.5s;

    @media (max-width: (575px)) {
      height: 50px;
      width: 50px;
    }
  }

  &__text {
    max-width: 325px;
    @include line-clamp(3);
    font-weight: 400;
  }

  &__btn {
    color: $color-acсent;
    border-radius: 50px;
    margin-bottom: 40px;
  }

  &__link {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
  }
}
