.footer {
  padding-bottom: 80px;

  &__inner {
    display: flex;
    justify-content: space-between;

    @media (max-width: (991px)) {
      flex-wrap: wrap;
    }
  }

  &__left {
    max-width: 300px;
    width: 100%;
    margin-right: 10px;

    @media (max-width: (991px)) {
      margin-bottom: 40px;
    }
  }

  &__text {
    @include line-clamp(4);
    font-weight: 400;
    letter-spacing: 1.15px;
    max-width: 306px;
    width: 100%;
    margin-bottom: 24px;
  }

  &__social {
    display: flex;
    max-width: 184px;
    width: 100%;
  }

  &__social-item {
    &:not(:last-child) {
      margin-right: 30px;
    }
  }

  &__social-link {
    outline-color: $color-bg;
  }

  &__social-img {
    height: 20px;

    &--facebook {
      width: 20px;
    }

    &--twitter {
      width: 25px;
    }

    &--youtube {
      width: 30px;
    }

    &--linkedin {
      width: 21px;
    }
  }

  &__right {
    display: flex;
    justify-content: space-between;
    max-width: 590px;
    width: 100%;

    @media (max-width: (575px)) {
      flex-wrap: wrap;
      justify-content: center;
    }
  }

  &__item {
    max-width: 155px;
    width: 100%;

    &:not(:last-child) {
      margin-right: 10px;
    }

    @media (max-width: (575px)) {
      margin-bottom: 20px;
      text-align: center;
    }
  }

  &__title {
    font-size: 21px;
    margin-bottom: 20px;
  }

  &__list-item {
    margin-bottom: 15px;
  }

  &__link {
    font-size: 14px;
    font-weight: 400;
    border-bottom: 1px solid transparent;
    transition: border-color 0.3s;

    &:hover {
      border-color: $color-black;
    }
  }
}
