.header {
  padding: 35px 0;

  &__nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__menu-list {
    display: flex;

    @media (max-width: (991px)) {
      display: block;
      position: absolute;
      top: 100px;
      left: 0;
      right: 0;
      background-color: $color-white;
      text-align: center;
      opacity: 0;
      transition: opacity 0.7s;
      z-index: 10;
    }

    &--active {
      opacity: 1;
    }
  }

  &__menu-item {
    font-weight: 400;

    &:not(:last-child) {
      margin-right: 42px;
    }

    @media (max-width: (991px)) {
      &:not(:last-child) {
        margin-right: 0px;
      }
    }
  }

  &__menu-link {
    border-bottom: 1px solid transparent;
    transition: color 0.3s, border-color 0.3s;

    &--active {
      color: $color-acсent;
      font-weight: 700;
    }

    &:active {
      color: $color-acсent;
      font-weight: 700;
    }

    &:hover {
      border-color: $color-text;
    }

    @media (max-width: (991px)) {
      display: block;
      padding: 12px 0;
    }
  }
}

.burger {
  @include burger;
  display: none;

  @media (max-width: (991px)) {
    display: block;
  }
}
