.use {
  margin-bottom: 50px;

  &__inner {
    min-height: 465px;
    height: 100%;
    background-image: url(../img/use-bg.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    border-radius: 20px;
    border-bottom-left-radius: 0;
    margin-bottom: -270px;
    z-index: 100;
    position: relative;

    @media (max-width: (991px)) {
      background-image: none;
      background-color: $color-bg;
      border-radius: 20px;
      justify-content: center;
      min-height: 350px;
    }
  }

  &__content {
    color: $color-white;
    max-width: 367px;
    margin-right: 95px;

    @media (max-width: (991px)) {
      margin: 0 20px;
    }

    @media (max-width: (575px)) {
      text-align: center;
    }
  }

  &__title {
    margin-bottom: 10px;
    @include line-clamp(2);
  }

  &__text {
    font-weight: 400;
    max-width: 300px;
    margin-bottom: 20px;
    @include line-clamp(3);
  }

  &__wrapper {
    display: flex;

    @media (max-width: (575px)) {
      flex-wrap: wrap;
      justify-content: center;
    }
  }


  .main-btn {
    margin-right: 20px;

    @media (max-width: (575px)) {
      margin: 0 10px;
      margin-bottom: 10px;
    }
  }

  .alt-btn {
    padding: 0;
  }

  &__btn {
    padding: 16px 29px 16px 49px;
    position: relative;
    color: $color-black;
    border-radius: 25px;
    background-color: $color-white;

    &::before {
      content: '';
      position: absolute;
      width: 12px;
      height: 12px;
      top: 23px;
      left: 30px;
      background-image: url(../img/watch-icon.svg);
      background-repeat: no-repeat;
      background-size: cover;
      background-position: center;

      @media (max-width: (575px)) {
        top: 15px;
        left: 15px;
      }
    }

    @media (max-width: (575px)) {
      height: 40px;
      padding: 9px 19px 9px 29px;
      font-size: 14px;
      display: inline;
    }
  }

}
