@import "../mixins/line-clump";

.hiro {
  margin-bottom: 100px;

  @media (max-width: (991px)) {
    margin-bottom: 50px;
  }

  &__container {
    display: flex;
    align-items: center;
  }

  &__content {
    z-index: 1;
    display: inline-block;
    margin-right: -110px;
    position: relative;

    @media (max-width: (991px)) {
      margin-right: 10px;
    }
  }

  &__title {
    max-width: 660px;
    @include line-clamp(4);
    font-size: 54px;
    line-height: 1.46;

    @media (max-width: (991px)) {
      font-size: 36px;
    }

    @media (max-width: (575px)) {
      font-size: 28px;
    }
  }

  &__subtitle {
    @include line-clamp(4);
    max-width: 466px;
    font-weight: 400;
    margin-bottom: 35px;

    @media (max-width: (575px)) {
      font-size: 14px;
    }
  }

  &__form {
    max-width: 456px;
    overflow: hidden;
    position: relative;

    &::before {
      content: '';
      position: absolute;
      left: 30px;
      top: 21px;
      height: 19px;
      width: 24px;
      z-index: 1;
      background-image: url(../img/email.svg);
      background-repeat: no-repeat;
      background-size: cover;
      background-position: center;
    }
  }

  &__input {
    border: none;
    max-width: 300px;
    width: 100%;
    height: 58px;
    box-shadow: -20px 25px 42px rgba(0, 0, 0, 0.12);
    border-bottom-left-radius: 29px;
    padding-left: 60px;
    padding-right: 10px;
    outline-color: $color-bg;

    &::placeholder {
      color: $color-grey;
      font-weight: 400;
    }

    @media (max-width: (991px)) {
      box-shadow: none;
      margin-bottom: 10px;
    }
  }

  &__btn {
    box-shadow: 40px 25px 42px rgba(0, 0, 0, 0.12);
    max-width: 152px;

    @media (max-width: (991px)) {
      box-shadow: none;
      margin-left: 20px;
    }
  }

  &__img {
    max-width: 540px;
    max-height: 676px;
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
    border-radius: 25px;
    border-bottom-left-radius: 0;
    z-index: 0;

    @media (max-width: (1199px)) {
      width: 400px;
      max-height: 500px;
    }

    @media (max-width: (767px)) {
      display: none;
    }
  }
}
