.partners {
  margin-bottom: 100px;

  @media (max-width: (991px)) {
    margin-bottom: 50px;
  }

  @media (max-width: (575px)) {
    display: none;
  }

  &__link {
    outline-color: $color-bg;
  }

  .slick-track {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .slick-slide {
    width: auto !important;
  }

}
