.choose {
  text-align: center;
  margin-bottom: 170px;

  @media (max-width: (991px)) {
    margin-bottom: 50px;
  }

  &__title {
    margin-bottom: 60px;

    @media (max-width: (991px)) {
      margin-bottom: 30px;
    }
  }

  &__list {
    display: flex;
    justify-content: space-between;

    @media (max-width: (991px)) {
      flex-wrap: wrap;
      justify-content: center;
    }
  }

  &__item {
    max-width: 250px;
    margin: 0 20px;
  }

  &__svg {
    width: 70px;
    height: 70px;

    @media (max-width: (991px)) {
      height: 50px;
      width: 50px;
    }
  }

  &__subtitle {
    font-size: 28px;

    @media (max-width: (991px)) {
      font-size: 22px;
    }

    @media (max-width: (575px)) {
      font-size: 18px;
    }
  }

  &__text {
    font-weight: 400;
    margin-top: 0;

    @media (max-width: (575px)) {
      font-size: 14px;
    }
  }
}
